.circles {
    position: relative;
    height: 1px;
}

.whitecircle {
    position: absolute;
    width: 168.67px;
    height: 168.67px;
    border-radius: 50%;
    border: 1.5px solid rgba(128, 128, 128, 0.5);
    transform: translateX(-50%);
    top: -230px;
    z-index: 0;
}

.whitecircle2 {
    position: absolute;
    border-radius: 50%;
